ta base a través de la exploración y el estudio independiente. La claridad en los objetivos es aún más crítica en entornos virtuales donde la observación directa de los procesos de aprendizaje puede ser desafiante.")])])]),_c('div',{staticClass:"row p-4 px-md-5 py-lg-5 justify-content-between"},[_c('div',{staticClass:"d-none d-lg-block col-md-6 col-lg-5",attrs:{"data-aos":"zoom-in"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-44.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-12 col-lg-7 mb-4 mb-md-0 d-flex flex-column justify-content-center"},[_c('p',{staticClass:"text-bold"},[_vm._v("Contenidos")]),_c('p',[_vm._v("Los contenidos, que constituyen el qué del aprendizaje, deben ser organizados y presentados de manera lógica y secuencial en un entorno virtual. La naturaleza del e-learning requiere orientar los contenidos según su tipo (conceptual, procedimental, actitudinal) para asegurar una experiencia de aprendizaje holística. La organización consecutiva y clara del contenido en línea es crucial para facilitar un aprendizaje efectivo en ausencia de una guía presencial constante.")])])]),_c('div',{staticClass:"row p-4 px-md-5 py-lg-5 justify-content-between"},[_c('div',{staticClass:"d-none d-lg-block col-md-6 col-lg-5",attrs:{"data-aos":"zoom-in"}},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-45.png"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-12 col-lg-7 mb-4 mb-md-0 d-flex flex-column justify-content-center"},[_c('p',{staticClass:"text-bold"},[_vm._v("Metodología")]),_c('p',[_vm._v("La metodología, o el cómo de la enseñanza, debe ser inherentemente activa y participativa en la educación virtual para fomentar un aprendizaje profundo. Priorizar experiencias que faciliten una comprensión global de los temas y que posibiliten trabajos más específicos es fundamental. La selección de estrategias y técnicas didácticas apropiadas para el entorno en línea 